.circlephoto {
	display: inline-block;
	float: left;
	width: 110px;
	height: 110px;
	background: #7F6DF0;
    overflow: hidden;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    margin-left: 73px;
}
.circlephoto img {
    display: block;
    width:100%;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
}
